import React from 'react';
import Form from 'antd/es/form';
import Input from 'antd/es/input';
import Row from 'antd/es/row';
import Col from 'antd/es/col';
import AuthButton from '../auth-button';
const FormItem = Form.Item;
class SearchForm extends React.Component {
    constructor() {
        super(...arguments);
        this.handleSubmit = (e) => {
            e.preventDefault();
            const { form } = this.props;
            form.validateFields((err, values) => {
                if (!err) {
                    for (const p in values) {
                        // 遍历json对象的每个key/value对,p为key
                        if (values[p] === '') {
                            values[p] = null;
                        }
                    }
                    this.props.handleSearch(values);
                }
            });
        };
        this.handleFormReset = () => {
            const { form, handleSearch } = this.props;
            form.resetFields();
            handleSearch(null);
        };
    }
    render() {
        const { form: { getFieldDecorator }, searchInitValue, } = this.props;
        const formItemLayout = {
            labelCol: { span: 6 },
            wrapperCol: { span: 18 },
        };
        return (<Form onSubmit={this.handleSubmit} layout="inline" className="SearchForm">
        <Row>
          <Col span={6}>
            <FormItem label="" {...formItemLayout}>
              {getFieldDecorator('keyword', {
            initialValue: searchInitValue.keyword,
            rules: [{ pattern: /^[^\s]*$/, message: '请不要输入空格' }],
        })(<Input placeholder="请输入关键字查询"/>)}
            </FormItem>
          </Col>
          <Col span={4} className="operation">
            <AuthButton style={{ marginLeft: -50 }} type="primary" htmlType="submit">
              查询
            </AuthButton>
            <AuthButton style={{ marginLeft: 0 }} onClick={this.handleFormReset}>
              重置
            </AuthButton>
          </Col>
        </Row>
      </Form>);
    }
}
const create = Form.create;
export default create()(SearchForm);
